import {GeoJSON} from "ol/format"
 */
import {apply} from "ol-mapbox-style";

// importing the OpenLayers stylesheet is required for having
// good looking buttons!
import 'ol/ol.css'

const jsonAPI = "https://api.maptiler.com/maps/d2920101-bc48-4e66-bd63-4f2decfe016c/style.json?key=EFL8VQIXbp5rTIyeRLOy"
export default {
    name: 'MapContainer',
    components: {},
    props: {},
    mounted() {
        // this is where we create the OpenLayers map
        const map = new Map({
            target: this.$refs['map-root'],
            layers: [
            ],
            view: new View({
                center: [0,0],
                zoom: 2,
            }),
        })
        apply(map, jsonAPI)
    },
